import { Node } from '@baklavajs/core';
// import { store } from '../../main';

export default class MatrixNode extends Node {
  type = 'MatrixNode';
  // twoColumn = true;

  name = 'Matriz';

  constructor() {
    super();

    this.addInputInterface('Reset');

    this.addInputInterface('Prximo', undefined, undefined, {
      description:
        'Toda vez que ele recebe uma informao, ele passa para a proxima posio da matriz ',
    });

    this.addInputInterface('Imagem', undefined, undefined, {
      description:
        'Recebe a imagem da bandeja de ovo caso eu queira verificar se os ovos esto na bandeja',
    });

    this.addOutputInterface('Item', {
      description: 'Passa o item da posio atual da matriz pra frente',
    });

    this.addOutputInterface('Matriz', {
      description: 'Sai uma matriz com a imagem cortada de cada pedacinho',
    });
    this.addOutputInterface('XY', {
      description: 'Sai as coordenadas normalmente usadas no n de "Mover Eixo"',
    });

    this.addOutputInterface('Fim', {
      description: 'Essa saida  ativada quando o ultimo item da matriz  selecionado',
    });
    this.addOutputInterface('Erro', {
      description: 'Quando ele tenta rodar e por algum motivo no deu certo, essa saida recebe o erro',
    });

    this.addOption('matrix', 'MatrixDialog', null);

    this.addOption('color', undefined, '#00ff89');
    this.addOption('running', undefined, true);
  }
}
